import React from "react";
import { connect, ConnectedProps } from 'react-redux'
import {RootState, updateProjects} from "./actions";

const mapState = (state: RootState) => {
    return {
        projects: state.projects
    }
};

const mapDispatchToProps = {
    updateProjects
};

const connector = connect(mapState, mapDispatchToProps);

type PropsFromRedux = ConnectedProps<typeof connector>;

type Props = PropsFromRedux & {};

type UpdateProjectProps = {
    project: string,
    delete: () => any,
    update: (project: string) => any
}

class UpdateProject extends React.Component<UpdateProjectProps, {editing: boolean, val: string}> {

    private inputElem = React.createRef<HTMLInputElement>();

    constructor(props: UpdateProjectProps) {
        super(props);
        this.state = {
            editing: false,
            val: props.project
        };
    }

    render() {
        return (
            <li>
                <input type={'text'}
                       value={this.props.project}
                       disabled={!this.state.editing}
                       ref={this.inputElem}
                       onChange={ev => this.setState({val: ev.target.value})}
                />
                <button className={'danger'} onClick={() => this.props.delete()}>
                    Delete
                </button>
                <button disabled={this.state.editing}
                        onClick={() => this.setState({editing: true})}
                >
                    Edit
                </button>
                <button disabled={!this.state.editing}
                        onClick={() => {
                            this.props.update(this.state.val);
                            this.setState({editing: false});
                        }}
                >
                    Save
                </button>
                <button disabled={!this.state.editing}
                        onClick={() => {
                            if (this.inputElem.current) {
                                this.inputElem.current.value = this.props.project;
                            }
                            this.setState({editing: false});
                        }}
                >
                    Cancel
                </button>
            </li>
        );
    }

}

class UpdateProjects extends React.Component<Props, {}> {

    private newInput = React.createRef<HTMLInputElement>();

    constructor(props: Props) {
        super(props);
    }

    render() {
        return (
            <div className={"status-container"}>
                <h2>
                    Current Projects:
                </h2>
                <ul className={'projects'}>
                    {
                        this.props.projects.map((project, index) =>
                            <UpdateProject
                                project={project}
                                delete={() => {
                                    this.props.updateProjects([...this.props.projects].filter((elem, ind) => ind !== index));
                                }}
                                update={(val: string) => {
                                    const newProjects = [...this.props.projects];
                                    newProjects[index] = val;
                                    this.props.updateProjects(newProjects);
                                }}
                            />)
                    }
                    <li>
                        <input ref={this.newInput} type={'text'} placeholder={'New Project Name'} />
                        <button onClick={() => {
                            if (this.newInput.current) { // check that input fits params
                                this.props.updateProjects([...this.props.projects, this.newInput.current.value]);
                            }
                        }}>
                            Add
                        </button>
                    </li>
                </ul>
            </div>
        );
    }

}

export default connector(UpdateProjects);